<template>
  <b-card no-body v-if="!$lodash.isEmpty(show)" class="h-100">
    <b-card-img-lazy :src="imageSRC"></b-card-img-lazy>

    <b-card-body>
      <b-link :to="'/movie/' + show.id">
        <b-card-title>{{ show.name }}</b-card-title>
      </b-link>
      <b-card-sub-title class="mb-2">{{
        show.genres.join(" / ")
      }}</b-card-sub-title>
      <b-card-text>
        <div class="badge bg-warning" v-if="ratingAVG">
          <span class="">{{ ratingAVG }}</span>
        </div>
      </b-card-text>
    </b-card-body>

    <template v-slot:footer>
      <b-link :to="`movie/${show.id}`">Detail</b-link>
    </template>
  </b-card>
</template>

<script>
export default {
  props: ["show"],
  computed: {
    imageSRC() {
      return this.$lodash.has(this.show, "image") && this.show.image
        ? this.show.image.medium
        : `https://via.placeholder.com/210x295?text=${this.show.name}`;
    },
    ratingAVG() {
      return this.$lodash.has(this.show, "rating") && this.show.rating.average
        ? this.show.rating.average
        : false;
    },
  },
};
</script>

<style>
.truncate {
  width: 250px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>