<template>
  <b-container class="bv-example-row">
    <div class="heading my-5">
      <h1 class="my-3">Shows</h1>
      <p>maze api</p>
    </div>
    <hr />
    <b-row>
      <b-col md="4">
        <b-input-group class="mt-3">
          <b-form-input
            v-model="searchModel"
            placeholder="Search movie"
          ></b-form-input>
          <b-input-group-append v-if="searchModel">
            <b-button variant="danger" @click="searchModel = ''">X</b-button>
          </b-input-group-append>
        </b-input-group>
      </b-col>
    </b-row>

    <hr />

    <b-row>
      <b-col
        class="my-3 mx-auto"
        md="4"
        lg="3"
        v-for="show in filteredList"
        :key="show.id"
      >
        <movie-card :show="show"></movie-card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapActions } from "vuex";
import MovieCard from "../components/MovieCard";
export default {
  components: {
    MovieCard,
  },
  data() {
    return {
      shows: [],
      searchModel: "",
      searchShowsList: [],
    };
  },
  mounted() {
    this.getRandomShowsList();
  },
  computed: {
    filteredList() {
      return !this.searchModel
        ? this.$store.getters.shows
        : this.searchShowsList;
    },
  },
  methods: {
    ...mapActions(["getRandomShowsList", "searchShow"]),
  },
  watch: {
    searchModel(new_val) {
      this.searchShow(new_val).then((r) => {
        this.searchShowsList = r.map((show) => show.show);
      });
    },
  },
};
</script>

